<template>
  <div class="recherche">
      <form v-on:submit.prevent="onSubmit">
          <div class="check">
            <div>
                <input type="checkbox" id="Entite" name="Entite"  value="Entite" v-model="checked">
                <label for="Entite">Entite</label>
            </div>
            <div>
                <input type="checkbox" id="Nomen" name="Nomen" value="Nomen" v-model="checked">
                <label for="Nomen">Nomen</label>
            </div>
            <div>
                <input type="checkbox" id="Depense" name="Depense" value="Depense" v-model="checked">
                <label for="Depense">Depense</label>
            </div>
            <div>
                <input type="checkbox" id="Inves" name="Inves" value="Inves" v-model="checked">
                <label for="Inves">Inves</label>
            </div>
            <div>
                <input type="checkbox" id="CodeC" name="CodeC" value="CodeC" v-model="checked">
                <label for="CodeC">CodeC</label>
            </div>
            <div>
                <input type="checkbox" id="LibelleC" name="LibelleC" value="LibelleC" v-model="checked">
                <label for="LibelleC">LibelleC</label>
            </div>
            <div>
                <input type="checkbox" id="CodeA" name="CodeA" value="CodeA" v-model="checked">
                <label for="CodeA">CodeA</label>
            </div>
            <div>
                <input type="checkbox" id="LibelleA" name="LibelleA" value="LibelleA" v-model="checked">
                <label for="LibelleA">LibelleA</label>
            </div>
            <div>
                <input type="checkbox" id="CodeP" name="CodeP" value="CodeP" v-model="checked">
                <label for="CodeP">CodeP</label>
            </div>
            <div>
                <input type="checkbox" id="LibelleP" name="LibelleP" value="LibelleP" v-model="checked">
                <label for="LibelleP">LibelleP</label>
            </div>
            <div>
                <input type="checkbox" id="Ordre" name="Ordre" value="Ordre" v-model="checked">
                <label for="Ordre">Ordre</label>
            </div>
            <div>
                <input type="checkbox" id="Montant" name="Montant" value="Montant" v-model="checked">
                <label for="Montant">Montant</label>
            </div>
          </div>
          <div class="content">
                <div class="input-1">
                    <label for="CodeArticle">CodeArticle</label>
                    <input v-model="inputCodeA" type="number" name="CodeArticle" id="CodeArticle" autocomplete="off">
                </div>
                <div class="input-2">
                    <label for="CodePresentationCroisee">CodePresentationCroisee</label>
                    <input v-model="inputCodeP" type="number" name="CodePresentationCroisee" id="CodePresentationCroisee">
                </div>
                <div class="input-3">
                    <label for="MontantRealise">MontantRealise</label>
                    <input v-model="inputMontant" type="number" name="MontantRealise" id="MontantRealise">
                </div>
                <div class="input-4">
                    <label for="inf">Infrieur</label>
                    <input type="radio" id="inf" name="trie" value="inf" v-model="radiopicked">
                </div>
                <div class="input-5">
                    <label for="sup">Suprieur</label>
                    <input type="radio" id="sup" name="trie" value="sup" v-model="radiopicked">
                </div>
                <div class="select-1">
                    <label for="EntiteFinanciereProgramme">EntiteFinanciereProgramme</label>
                    <select name="EntiteFinanciereProgramme" id="EntiteFinanciereProgramme" v-model="selectEntite">
                        <option v-for="e in entite" :key="e">{{e}}</option>
                    </select>
                </div>
                <div class="select-2">
                    <label for="NomenclatureComptable">NomenclatureComptable</label>
                    <select name="NomenclatureComptable" id="NomenclatureComptable" v-model="selectNomen">
                        <option v-for="e in nomenclature" :key="e">{{e}}</option>
                    </select>
                </div>
                <div class="select-3">
                    <label for="DEPENSES_RECETTES">DEPENSES_RECETTES</label>
                    <select name="DEPENSES_RECETTES" id="DEPENSES_RECETTES" v-model="selectDepense">
                        <option v-for="e in depense" :key="e">{{e}}</option>
                    </select>
                </div>
                <div class="select-4">
                    <label for="INVESTISSEMENT_FONCTIONNEMENT">INVESTISSEMENT_FONCTIONNEMENT</label>
                    <select name="INVESTISSEMENT_FONCTIONNEMENT" id="INVESTISSEMENT_FONCTIONNEMENT" v-model="selectInves">
                        <option v-for="e in inves" :key="e">{{e}}</option>
                    </select>
                </div>
                <div class="select-5">
                    <label for="CodeChapitre">CodeChapitre</label>
                    <select name="CodeChapitre" id="CodeChapitre" v-model="selectCodeC">
                        <option v-for="e in codeC" :key="e">{{e}}</option>
                    </select>
                </div>
                <div class="select-6">
                    <label for="ORDRE_REEL">ORDRE_REEL</label>
                    <select name="ORDRE_REEL" id="ORDRE_REEL" v-model="selectOrdre">
                        <option v-for="e in ordre" :key="e">{{e}}</option>
                    </select>
                </div>
              </div>
              <button>Recherche</button>
      </form>
      <div class="resultat">
          <table>
            <tr>
                <th v-if="checked.includes('Entite')">EntiteFinanciereProgramme</th>
                <th v-if="checked.includes('Nomen')">NomenclatureComptable</th>
                <th v-if="checked.includes('Depense')">DEPENSES_RECETTES</th>
                <th v-if="checked.includes('Inves')">INVESTISSEMENT_FONCTIONNEMENT</th>
                <th v-if="checked.includes('CodeC')">CodeChapitre</th>
                <th v-if="checked.includes('LibelleC')">LibelleChapitre</th>
                <th v-if="checked.includes('CodeA')">CodeArticle</th>
                <th v-if="checked.includes('LibelleA')">LibelleArticle</th>
                <th v-if="checked.includes('CodeP')">CodePresentationCroisee</th>
                <th v-if="checked.includes('LibelleP')">LibellePresentationCroisee</th>
                <th v-if="checked.includes('Ordre')">ORDRE_REEL</th>
                <th v-if="checked.includes('Montant')">MontantRealise</th>
            </tr>
            <tr v-for="(e,index) in res" :key="index">
                <td v-if="checked.includes('Entite')">{{e['EntiteFinanciereProgramme']}}</td>
                <td v-if="checked.includes('Nomen')">{{e['NomenclatureComptable']}}</td>
                <td v-if="checked.includes('Depense')">{{e['DEPENSES_RECETTES']}}</td>
                <td v-if="checked.includes('Inves')">{{e['INVESTISSEMENT_FONCTIONNEMENT']}}</td>
                <td v-if="checked.includes('CodeC')">{{e['CodeChapitre']}}</td>
                <td v-if="checked.includes('LibelleC')">{{e['LibelleChapitre']}}</td>
                <td v-if="checked.includes('CodeA')">{{e['CodeArticle']}}</td>
                <td v-if="checked.includes('LibelleA')">{{e['LibelleArticle']}}</td>
                <td v-if="checked.includes('CodeP')">{{e['CodePresentationCroisee']}}</td>
                <td v-if="checked.includes('LibelleP')">{{e['LibellePresentationCroisee']}}</td>
                <td v-if="checked.includes('Ordre')">{{e['ORDRE_REEL']}}</td>
                <td v-if="checked.includes('Montant')">{{e['MontantRealise']}}</td>
            </tr>
            </table>
      </div>
  </div>
</template>
<script>
export default {
  name: 'Recherche',
  props:['donne'],
  data(){
      return{
          entite:[''],
          nomenclature:[''],
          depense:[''],
          inves:[''],
          codeC:[''],
          ordre:[''],
          inputCodeA:'',
          inputCodeP:'',
          inputMontant:'',
          radiopicked:'inf',
          selectEntite:'',
          selectNomen:'',
          selectDepense:'',
          selectInves:'',
          selectCodeC:'',
          selectOrdre:'',
          res:[],
          checked:[]
      }
  },
  methods:{
      onSubmit(){
          this.res=[];
          let tmp=this.donne.slice();
          if(this.inputCodeA!=''){
              tmp=tmp.filter(e => e['CodeArticle']==this.inputCodeA);
          }
          if(this.inputCodeP!=''){
              tmp=tmp.filter(e => e['CodePresentationCroisee']==this.inputCodeP);
          }
          if(this.inputMontant!=''){
              tmp=tmp.filter(e => {
                  if(this.radiopicked=='inf'){
                      return e['MontantRealise']<=this.inputMontant;
                  }
                  if(this.radiopicked=='sup'){
                      return e['MontantRealise']>=this.inputMontant;
                  }
                  return e['MontantRealise']==this.inputMontant;
              });
          }
          if(this.selectEntite!=''){
              tmp=tmp.filter(e => e['EntiteFinanciereProgramme']==this.selectEntite);
          }
          if(this.selectNomen!=''){
              tmp=tmp.filter(e => e['NomenclatureComptable']==this.selectNomen);
          }
          if(this.selectDepense!=''){
              tmp=tmp.filter(e => e['DEPENSES_RECETTES']==this.selectDepense);
          }
          if(this.selectInves!=''){
              tmp=tmp.filter(e => e['INVESTISSEMENT_FONCTIONNEMENT']==this.selectInves);
          }
          if(this.selectCodeC!=''){
              tmp=tmp.filter(e => e['CodeChapitre']==this.selectCodeC);
          }
          if(this.selectOrdre!=''){
              tmp=tmp.filter(e => e['ORDRE_REEL']==this.selectOrdre);
          }
          this.res=tmp;
      }
  },
  mounted: function () {
      this.donne.forEach(e => {
          if(!this.entite.includes(e['EntiteFinanciereProgramme'])){
              this.entite.push(e['EntiteFinanciereProgramme'])
          }
          if(!this.nomenclature.includes(e['NomenclatureComptable'])){
              this.nomenclature.push(e['NomenclatureComptable'])
          }
          if(!this.depense.includes(e['DEPENSES_RECETTES'])){
              this.depense.push(e['DEPENSES_RECETTES'])
          }
          if(!this.inves.includes(e['INVESTISSEMENT_FONCTIONNEMENT'])){
              this.inves.push(e['INVESTISSEMENT_FONCTIONNEMENT'])
          }
          if(!this.codeC.includes(e['CodeChapitre'])){
              this.codeC.push(e['CodeChapitre'])
          }
          if(!this.ordre.includes(e['ORDRE_REEL'])){
              this.ordre.push(e['ORDRE_REEL'])
          }
      });
  }
}
</script>

<style scoped>
.recherche{
    width: 100%;
    height: 100%;
}
input,select{
    outline: none;
}
form{
    border-bottom: 1px solid #000;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
form .content{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}
.content div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
button{
    padding: 10px 10px;
    margin: 5px 0;
}
.resultat{
    width: 100%;
    height: 400px;
    overflow: scroll;
}
table {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

table td, table th {
  border: 1px solid #ddd;
  padding: 8px;
}

table tr:nth-child(even){background-color: #f2f2f2;}

table tr:hover {background-color: #ddd;}

table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #4CAF50;
  color: white;
}
.check{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}
</style>
