<template>
  <div class="ajoute">
      <form v-on:submit.prevent="onSubmit">
          <div class="content">
                <div class="select-1">
                    <label for="EntiteFinanciereProgramme">EntiteFinanciereProgramme</label>
                    <select name="EntiteFinanciereProgramme" id="EntiteFinanciereProgramme" v-model="selectEntite" required>
                        <option v-for="e in entite" :key="e">{{e}}</option>
                    </select>
                </div>
                <div class="select-2">
                    <label for="NomenclatureComptable">NomenclatureComptable</label>
                    <select name="NomenclatureComptable" id="NomenclatureComptable" v-model="selectNomen" required>
                        <option v-for="e in nomenclature" :key="e">{{e}}</option>
                    </select>
                </div>
                <div class="select-3">
                    <label for="DEPENSES_RECETTES">DEPENSES_RECETTES</label>
                    <select name="DEPENSES_RECETTES" id="DEPENSES_RECETTES" v-model="selectDepense" required>
                        <option v-for="e in depense" :key="e">{{e}}</option>
                    </select>
                </div>
                <div class="select-4">
                    <label for="INVESTISSEMENT_FONCTIONNEMENT">INVESTISSEMENT_FONCTIONNEMENT</label>
                    <select name="INVESTISSEMENT_FONCTIONNEMENT" id="INVESTISSEMENT_FONCTIONNEMENT" v-model="selectInves" required>
                        <option v-for="e in inves" :key="e">{{e}}</option>
                    </select>
                </div>
                <div class="select-5">
                    <label for="CodeChapitre">CodeChapitre</label>
                    <input v-model="selectCodeC" type="number" name="CodeChapitre" id="CodeChapitre" autocomplete="off" required>
                </div>
                <div class="text-1">
                    <label for="LibelleChapitre">LibelleChapitre</label>
                    <textarea name="LibelleChapitre" id="LibelleChapitre" cols="40" rows="3" v-model="libelle1"></textarea>
                </div>
                <div class="input-1">
                    <label for="CodeArticle">CodeArticle</label>
                    <input v-model="inputCodeA" type="number" name="CodeArticle" id="CodeArticle" autocomplete="off" required>
                </div>
                <div class="text-2">
                    <label for="LibelleArticle">LibelleArticle</label>
                    <textarea name="LibelleArticle" id="LibelleArticle" cols="40" rows="3" v-model="libelle2"></textarea>
                </div>
                <div class="input-2">
                    <label for="CodePresentationCroisee">CodePresentationCroisee</label>
                    <input v-model="inputCodeP" type="number" name="CodePresentationCroisee" id="CodePresentationCroisee" required>
                </div>
                <div class="text-3">
                    <label for="LibellePresentationCroisee">LibellePresentationCroisee</label>
                    <textarea name="LibellePresentationCroisee" id="LibellePresentationCroisee" cols="40" rows="3" v-model="libelle3" ></textarea>
                </div>
                <div class="select-6">
                    <label for="ORDRE_REEL">ORDRE_REEL</label>
                    <select name="ORDRE_REEL" id="ORDRE_REEL" v-model="selectOrdre" required>
                        <option v-for="e in ordre" :key="e">{{e}}</option>
                    </select>
                </div>
                <div class="input-3">
                    <label for="MontantRealise">MontantRealise</label>
                    <input v-model="inputMontant" type="number" name="MontantRealise" id="MontantRealise" required>
                </div>
              </div>
              <button>Ajouter</button>
      </form>
  </div>
</template>

<script>

export default {
  name: 'Ajoute',
  props:['donne'],
    data(){
      return{
          entite:[],
          nomenclature:[],
          depense:[],
          inves:[],
          codeC:[],
          ordre:[],
          libelle1:'',
          libelle2:'',
          libelle3:'',
          inputCodeA:'',
          inputCodeP:'',
          inputMontant:'',
          selectEntite:'',
          selectNomen:'',
          selectDepense:'',
          selectInves:'',
          selectCodeC:'',
          selectOrdre:'',
      }
  },
  methods:{
      onSubmit(){
          let arr={
            "EntiteFinanciereProgramme": this.selectEntite,
            "NomenclatureComptable": this.selectNomen,
            "DEPENSES_RECETTES": this.selectDepense,
            "INVESTISSEMENT_FONCTIONNEMENT": this.selectInves,
            "CodeChapitre": this.selectCodeC,
            "LibelleChapitre": this.libelle1,
            "CodeArticle": this.inputCodeA,
            "LibelleArticle": this.libelle2,
            "CodePresentationCroisee": this.selectCodeC,
            "LibellePresentationCroisee": this.libelle3,
            "ORDRE_REEL": this.selectOrdre,
            "MontantRealise": this.inputMontant
          };
          this.$emit('ajouter-donne',arr);
          this.reset();
      },
      reset(){
          this.libelle1='';
          this.libelle2='';
          this.libelle3='';
          this.inputCodeA='';
          this.inputCodeP='';
          this.inputMontant='';
          this.selectEntite='';
          this.selectNomen='';
          this.selectDepense='';
          this.selectInves='';
          this.selectCodeC='';
          this.selectOrdre='';
      }
  },
  mounted: function () {
      this.donne.forEach(e => {
          if(!this.entite.includes(e['EntiteFinanciereProgramme'])){
              this.entite.push(e['EntiteFinanciereProgramme'])
          }
          if(!this.nomenclature.includes(e['NomenclatureComptable'])){
              this.nomenclature.push(e['NomenclatureComptable'])
          }
          if(!this.depense.includes(e['DEPENSES_RECETTES'])){
              this.depense.push(e['DEPENSES_RECETTES'])
          }
          if(!this.inves.includes(e['INVESTISSEMENT_FONCTIONNEMENT'])){
              this.inves.push(e['INVESTISSEMENT_FONCTIONNEMENT'])
          }
          if(!this.codeC.includes(e['CodeChapitre'])){
              this.codeC.push(e['CodeChapitre'])
          }
          if(!this.ordre.includes(e['ORDRE_REEL'])){
              this.ordre.push(e['ORDRE_REEL'])
          }
      });
  }
}
</script>

<style scoped>
.ajoute{
    width: 100%;
    height: 100%;
}
input,select,textarea{
    outline: none;
}
form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
form .content{
    display: block;
    width: 80%;
}
form .content div{
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
}
button{
    padding: 10px 10px;
    margin: 5px 0;
}
select{
    width: 300px;
}
</style>
