<template>
  <div id="app">
    <header class="banner">
      <h2 class="banner-text" v-if="this.login!=''">Bonjour {{login}}</h2>
      <button v-if="this.login!=''" @click="logout">Dconnecter</button>
    </header>
    <Nav :login="login" @change-tab="changeTab"></Nav>
    <div class="vue">
        <component :is="composantOngletActuel"  @log-sucess="afficher" @ajouter-donne="ajouter" :donne="donne" :key="this.donne.length"></component>
    </div>
  </div>
</template>

<script>
import Nav from "./components/Nav"
import Recherche from "./components/Recherche"
import Ajoute from "./components/Ajoute"
import Login from "./components/Login"
import adm from './adm.js'
export default {
  name: 'App',
  data(){
    return {
      composantOngletActuel:'Login',
      login:'',
      pass:'',
      donne:[],
    }
  },
  components: {
    Nav,
    Recherche,
    Ajoute,
    Login
  },
  methods:{
    afficher(e1,e2){
      this.login=e1;
      this.pass=e2;
      this.composantOngletActuel='Recherche';
    },
    logout(){
      this.login='';
      this.pass='';
      this.composantOngletActuel='Login';
    },
    changeTab(e){
      this.composantOngletActuel=e;
    },
    ajouter(e){
      const banner=document.querySelector('.banner');
      const bannerText=document.querySelector('.banner-text');
      let tmpColor='linear-gradient(90deg, rgba(42,123,192,1) 0%, rgba(57,190,113,1) 100%)';
      let tmpText=bannerText.innerHTML;
      this.donne.push(e);
      setTimeout(()=>{
        banner.style.background='green';
        bannerText.innerHTML='Ajoute donne succs';
      },500)
      setTimeout(()=>{
        banner.style.background=tmpColor;
        bannerText.innerHTML=tmpText;
      },3000)
    }
  },
  mounted: function () {
    this.donne=adm.slice();
  }
}
</script>

