  margin: 0;
  padding: 0;
  box-sizing:border-box;
  font-family: 'Poppins', sans-serif;
}
#app{
  position: relative;
  width: 100vw;
  min-height: 100vh;
}
header{
  width: 100%;
  height: 8vh;
  background: linear-gradient(90deg, rgba(42,123,192,1) 0%, rgba(57,190,113,1) 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 1s ease-in-out;
}
header h2{
  margin-left: auto;
  color: #fff;
  transition: all 1s ease-in-out;
}
header button{
  margin-left: auto;
  margin-right: 10px;
  text-decoration: none;
  padding: 10px 5px;
  border: none;
  outline: none;
  border-radius: 5px;
  background: #fff;
}
.vue{
  position: absolute;
  top: 8vh;
  left: 10vw;
  background:rgb(244, 244, 244);
  width: calc(100vw - 10vw);
  height: calc(100vh - 8vh);
}
</style>
