<template>
  <div class="login">
      <div class="log">
          <form v-on:submit.prevent="onSubmit">
              <div class="input-1">
                <label for="name">Username</label>
                <input v-model="log" type="text" name="name" id="name" autocomplete="off" required>
              </div>
              <div class="input-2">
                <label for="pass">Password</label>
                <input v-model="pass" type="password" name="pass" id="pass" required>
              </div>
              <button>Login</button>
          </form>
      </div>
  </div>
</template>

<script>

export default {
  name: 'Login',
  data(){
      return{
          log:'',
          pass:''
      }
  },
  methods:{
      onSubmit(){
        this.$emit('log-sucess',this.log,this.pass);
        this.log='';
        this.pass='';
      }
  }
}
</script>

<style scoped>
.login{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.log{
    width: 50%;
    height: 70%;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}
.log form{
    display: flex;
    flex-direction: column;
}
.input-1,.input-2{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.input-2,button{
    margin-top: 10px;
}
#name,#pass{
    outline: none;
}
</style>
